/* CSS files add styling rules to your content */

body {
  font-family: helvetica, arial, sans-serif;
  margin: 2em;
}

h1 {
  font-style: italic;
  color: #373fff;
}

table {
  margin-top: 10px;
  border-collapse: collapse;
}

table, tr, td {
  border: 1px solid black;
}

td {
  width: 200px;
}